<script>
	let paused = true
	let duration = 0, currentTime = 0
	
	export let title="unknown", artist = "unknown", imgSrc = "", imgAlt = "", audioSrc = "", currX = 0, currY = 0, color1 = 'plum', color2 = "powderblue";
	
	function formatTime(timeInSec) {
		let minutes = Math.floor(timeInSec / 60).toString().padStart(2, '0') || "00"
		let seconds = Math.floor(timeInSec % 60).toString().padStart(2, '0') || "00"
		
		return `${minutes}:${seconds}`
	}
	
	function mountAudio(node, bar) {
		// the node has been mounted in the DOM
		return {
			update(bar) {
				node.pause()
				paused = true
			}
		}
	}
</script>

<style>
	.player {
		--maxRot: 15deg;
		color: white;
		width: 50vmin;
		height: 60vmin;
		box-sizing: border-box;
		margin: 3vmin;
		padding: 5vmin;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		background: rgba(255,255,255,0.5);
		border-radius: 2vmin;
		--main-shadow: 0 2vmin 2.5vmin rgba(255,255,255,0.1),
									 0 .4vmin .6vmin rgba(255,255,255,0.2);
		box-shadow: var(--main-shadow);
		transform: perspective(800px) rotateY(var(--parallax-x)) rotateX(var(--parallax-y));
		transform-style: preserve-3d;
	}
	
	.player > * {
		transform: translateZ(40px);
		box-sizing: border-box;
	}
	
	.img-dot {
		--size: 25vmin;
		height: var(--size);
		width: var(--size);
		background: white;
		object-fit: cover;
		border-radius: 50%;
		box-shadow: var(--main-shadow);
	}
	
	.track-info {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: .2em 0;
	}
	
	.track-info h3, .track-info p {
		margin: 1vmin;
		padding: 0;
		font-weight: normal;
		font-size: calc(.5em + 1vmin);
		text-align: center;
	}
	.track-info p {
		font-size: calc(.5em + .8vmin);
		font-weight: bold;
		width: fit-content;
		position: relative;
	}

	.track-info p::before,
	.track-info p::after {
		content: '';
		height: 1px;
		position: absolute;
		right: 100%;
		top: 50%;
		width: 2vmin;
		background: white;
		margin: 0 1vmin;
	}
	.track-info p::after {
		right: initial;
		left: 100%;
	}
	
	small { font-size: .5em; }
	
	.controls {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}
	
	.controls button {
		border: none;
		background: transparent;
		color: white;
		font-size: calc(.5em + 1.4vmin);
	}
	
	.controls button:focus { outline: solid white; }
	.controls button:hover { background: rgba(255,255,255,0.4); }
	
	.timeline { width: 100%; }

	.progress-bar {
		--prog-w: 1%;
		background: white;
		width: 100%;
		--bar-h: 1vmin;
		height: var(--bar-h);
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: calc(var(--bar-h) / 2);
		overflow: hidden;
	}

	.progress-bar input {
		margin: 0;
		opacity: 0;
		width: 100%;
	}
	
	.progress-bar::before {
		position: absolute;
		content: '';
		height: 100%;
		top: 0;
		left: 0;
		width: var(--prog-width);
		border-radius: 1vmin;
		background: linear-gradient(to right, var(--hi-1), var(--hi-2));
	}
	
	.times {
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
</style>

<div class="player" style="--parallax-x: calc({currX} * var(--maxRot)); --parallax-y: calc({currY} * -1 * var(--maxRot));
													 --hi-1: {color1}; --hi-2: {color2};">
	<audio bind:duration bind:paused bind:currentTime src={audioSrc} use:mountAudio={audioSrc}>
		Your browser does not support the
		<code>audio</code> element.
	</audio>
	<img class="img-dot" src={imgSrc} alt={imgAlt}>
	<div class="track-info">
		<h3>{title}</h3>
		<p>{artist}</p>
	</div>
	<div class="controls">
		<button class="previous" on:click={() => currentTime -= 30}><small>30s</small>&lt;</button>
		<button class="play" on:click={() => paused = !paused }>{(paused) ? "Play" : "Pause"}</button>
		<button class="next" on:click={() => currentTime += 30}>&gt;<small>30s</small></button>
	</div>
	<div class="timeline">
		<div class="progress-bar" aria-label="audio progress bar control" style="--prog-width: {currentTime/duration*100}%;">
			<input type="range" min=0 max={duration} bind:value={currentTime}>
		</div>
		<div class="times">
			<span>{formatTime(currentTime)}</span>
			<span>{formatTime(duration)}</span>
		</div>		
	</div>
</div>
